.hero-section{
  display: flex;
  height: 100vh;
 align-items: center;
 justify-content: space-around;
 flex-wrap: wrap-reverse;
 width: 90%;

 margin: auto auto;
}
.bkg{
position: absolute;
width: 100%;
height: 100vh;

z-index: -1;
  img{
    // position: absolute;
    width: 100%;
    height: 100%;
    
  }

}
.hero-text-img{
  background-image: url('./assets/images/ellipses.png');
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
  top:120px;
  left:250px;
  width: 200px;
  height: 200px;
position: absolute;


}
.hero-text{



  h1{
   
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
   
    text-transform: uppercase;
    margin: 0px 0px;
    color: $SloganColor;
  }
  h2{
    margin: 0px 0px;
    font-style: normal;
font-weight: 700;
font-size: 30px;

color: $SloganColor;
text-transform: capitalize

  }
}
.hero-image{
  width: 500px;

  height: 400px;
  img{
    width: 100%;
    height: 100%;
  }
}

//responsive Section
@media screen and(max-width:1138px) {
  .hero-image{
    width: 300px;
    height: 200px;
  }
}

  .hero-text{
  h1{
     
    
      font-size: 30px;
     
    }
    h2{
      font-size: 20px;
    }
    .btn-getQuote{
      
    }
  
}