.button {
  padding: 0.5rem 1.5rem;
  border: 1px solid #FF7900;
  border-radius: 20px;
  background: #FF7900;
  color: #ffff;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
  font-weight: 800;
  font-size: 20px;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #683385;
  border-color: #683385;
}

.button--inverse {
  background: transparent;
  color: #683385;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #683385;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}

