.vision-section{
height: 100vh;
// background-image: url("./assets/images/vision-section-bg.jpg");
// background-size: cover;

.vision-container{
width: 90%;
margin: auto auto;

flex-wrap: wrap;
display: flex;
align-items: center;
justify-content: center;
}


}
.vision-background{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    

    img{
        width: 100%;
        height: 100%;
    }
}


.vision-text{
    width: 450px;

    h1{
      
        color: $VioletColor;
        font-weight: 700;
        font-size: 40px;
        line-height: 72px;
        text-transform: uppercase;
        
    }
    p{
        color: $VioletColor;
        font-weight: 900;
        font-size: 10px;
        text-align: justify;
        
    }
}
.vision-img{
    width: 400px;
    height: 400px;
    margin-top: 50px;
       

    img{
        width: 100%;
        height: 100%;
      
    }
}
.vison-arrow{
display: block;
    text-align: center;
    h2{
        color: $VioletColor;
        font-weight: 800;
        font-size: .6rem;
    }
    img{
        width: 10px;
    }
}

@media screen and(max-width:980px) {
    .vision-container{
        width: 80%;
    }
    .vision-text{
        width: 100%;
       margin-top: 25px;
       margin-left: 25px;
 
        h1{
            text-align: center;

            font-size: 30px;
        }
    }
    .vision-img{
        width: 600px;
        height: 180px;
           
    
        img{
            width: 100%;
            height: 100%;
          
        }
    }

    
}