.our-product{
    display: block;
    width: 100%;
    height: 100vh;
  
}
.our-product-background{
position: absolute;
height:100vh;
width: 100%;
z-index: -1;
img{
    width: 100%;
    height: 100%;
}
}
.product-title{
    width: 90%;
    margin: auto auto;
    align-items: flex-end;
    display: flex;
    border-bottom: 3px solid $OrangeColor;
   justify-content: space-around;
  
 
   padding-right: 50px;
    h1{
        flex:2;
        color:$VioletColor;
    }
    h2{
        font-size: 1rem;
    }

}
.product-container{
    // background-color: red;
    width: 80%;
    margin: 25px auto;
    height: 70%;
    display: flex;
    overflow: hidden hidden;
    justify-content: space-around;
   
  

  
}




.product-content{
    display: block;
    min-width:300px;
    height: 350px;
 

 position: relative;
 margin: 0 10px;
    
}
.product-image{
    width: 300px;
    height: 300px;
    img{
        width: 100%;
        height: 100%;
    }
    +h1{
        color: $VioletColor;
        font-weight: 600;
font-size: 25px;
text-align: center;
    }
}
.bottom-border{
    width: 80%;
    margin: auto auto;
    border: 3px dashed $OrangeColor;
}