.main-content{
    width: 100%;
    height: 100%;
    padding: 0 100px;
    display:flex;
    justify-content: space-between;
  
    align-items: center;

}
.logo{

   width: 100px;
   height: 4rem;
   margin-top: 0px;
   img{
       width: 100%;
       height: 100%;
   }
}
.nav-bar{
    display: flex;
    width: 100%;
 
}
.nav-links{
    list-style: none;
    
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    li{
        margin: 0 10px;
    }
    a{
        
        font-style: normal;
        font-weight:800;
        font-size: 18px;
        line-height: 16px;
        color: $VioletColor;
       text-decoration: none;
       padding: 0.5rem;
    }
 a.active,a:active,a:hover{
        border-bottom:2px solid $OrangeColor;
        font-weight: 600;
    }

}

@media screen and(max-width:1138px) {

    .nav-links{
        a{
            font-size: 10px;
        }
    }
}