.our-team-section{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    

}
.our-team-bkg{
    position: absolute;
    height: 100vh;
    z-index: -1;
    width: 100%;

    img{
        width: 100%;
        height: 100%;
    }
}
.our-team-container{
    width: 90%;
    height: 100vh;
    margin: auto auto;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    

}
.our-team-title{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 89%;
    margin: 0 auto;

    border-bottom: 3px solid $OrangeColor;
    h1{
        color: $VioletColor;
        font-size: 30px;
        font-weight: 800;
        font-style: normal;
    }
}
.teams-persons{
width: 100%;
height: 100%;
align-items: center;
display: flex;
justify-content: space-between;

}
.our-team-persons{
 
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 300px;  
    align-items: center;
    .person-image{
        border-radius: 50%;
        width: 150px;
        height: 150px;
        img{
            width: 100%;
            height: 100%;
        }
        +h1{
            color: $VioletColor;
        }
        
    }
}
.desc-team{
    width: 150px;
    text-align: justify;
    p{
        color: $OrangeColor;
        font-size: 1rem;
        font-weight: bolder;
     }

}
.team-line{
    height:300px;
    img{
        height: 100%;
    }
}