.services-sections{
    width: 100%;
    height:100vh;
  display: flex;

}
.services-bakcground{
    width: 100%;
    height: 100%;
    position: absolute;
 margin-top: -6px;
    z-index: -1;
    img{
    width: 100%;
    height: 100%;
    position: absolute;
  
    }}
.our-services{
    width: 70%;
    height: 100vh;

    text-align: center;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    
    h1{
        
        font-weight: 700;
        color: $VioletColor;
        font-size: 48px;
        line-height: 72px;
        border-bottom: 2px solid $OrangeColor;
   
    }
 
}
.our-services-container{
 
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
 
}
.services-img{
    margin-top: 10px;
    width: 80%;
    height: 50%;
    img{
        width: 100%;
        height: 100%;
    }
}
.services-text{
    text-align: justify;
    margin: auto 50px;
    
    p{
        color: $VioletColor;
        font-weight: 600;
        font-size: 1.2rem;
        text-align: justify;
   
    }
}
.our-services::-webkit-scrollbar{
  width: 7px;
}
.our-services::-webkit-scrollbar-track-piece{
    background-color: $VioletColor;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 290px solid transparent;
}
.our-services::-webkit-scrollbar-thumb{
    background-color:$OrangeColor;

    border-left: 4px solid $OrangeColor;
    border-right: 4px solid $OrangeColor;
    border-top: 290px solid $VioletColor;
    border-bottom: 290px solid $VioletColor;
  

}
.our-services::-webkit-scrollbar-corner{
    width: 1005px;
}
.services-title{
    align-self: center;

    h1{
        
        color: $VioletColor;
        font-size: 3rem;
       
    }
}
